"use client"

import { useState } from "react"
import OnboardingScreen from "@/components/onboarding-screen"
import AuthScreen from "@/components/auth-screen"
import Dashboard from "@/components/dashboard"

export default function Home() {
  const [currentScreen, setCurrentScreen] = useState<"onboarding" | "auth" | "dashboard">("onboarding")

  const handleOnboardingComplete = () => {
    setCurrentScreen("auth")
  }

  const handleAuthComplete = () => {
    setCurrentScreen("dashboard")
  }

  return (
    <main className="min-h-screen bg-background">
      {currentScreen === "onboarding" && <OnboardingScreen onComplete={handleOnboardingComplete} />}
      {currentScreen === "auth" && <AuthScreen onComplete={handleAuthComplete} />}
      {currentScreen === "dashboard" && <Dashboard />}
    </main>
  )
}
